.content{max-width:100%;line-height:1.7;color:var(--text-color, #333)}.heading1{font-size:2.5rem;font-weight:700;margin-bottom:1.5rem;margin-top:2rem;line-height:1.2}.heading1:first-child{margin-top:0}.heading2{font-size:2rem;font-weight:600;margin-bottom:1.25rem;margin-top:1.75rem;line-height:1.3}.heading2:first-child{margin-top:0}.heading3{font-size:1.5rem;font-weight:600;margin-bottom:1rem;margin-top:1.5rem;line-height:1.4}.heading3:first-child{margin-top:0}.paragraph{margin-bottom:1.5rem;font-size:1rem}.paragraph:last-child{margin-bottom:0}.blockquote{border-left:4px solid var(--accent-color, #007acc);padding-left:1.5rem;margin:2rem 0;font-style:italic;font-size:1.1rem;color:var(--muted-color, #666)}.strong{font-weight:700}.italic{font-style:italic}.underline{text-decoration:underline}.link{color:var(--accent-color, #007acc);text-decoration:none;border-bottom:1px solid rgba(0,0,0,0);transition:border-bottom-color .2s ease}.link:hover{border-bottom-color:var(--accent-color, #007acc)}@media(max-width: 768px){.heading1{font-size:2rem}.heading2{font-size:1.75rem}.heading3{font-size:1.25rem}.paragraph{font-size:.9rem}.blockquote{padding-left:1rem;margin:1.5rem 0;font-size:1rem}}